.App {
  text-align: center;
}

body {
  font-family: 'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
}

:root {
  --row-height: 40px;
  --picker-width: 300px;
}

#color-preview {
  width: var(--picker-width);
  height: 200px;
  margin: auto;
  margin-top: 20px;
  margin-bottom: 10px;
}

#color-guess {
  width: var(--picker-width);
  height: 200px;
  margin: auto;
  margin-top: 20px;
  margin-bottom: 10px;
}

#cheating-mode {
  position: absolute;
  right: 10px;
}

#color-picker {
  display: table;
  width: var(--picker-width);
  margin: auto;
}

#color-picker > .row {
  display: table-row;
  height: var(--row-height);
}

#color-picker > .row > .component-color-preview {
  display: table-cell;
  vertical-align: middle;
  padding-left: 3px;
  padding-right: 3px;
  text-align: right;
  font-weight: bold;
}

#color-picker input[type="number"] {
  width: 48px;
  height: var(--row-height);
  text-align: center;
}

#color-picker input[type="range"] {
  position: relative;
  top: 5px;
  background-color: red;
} 

p{
  text-align: center;
}